import './App.scss';
import { HashRouter, BrowserRouter, Link, Switch } from "react-router-dom";
import Footer from './Components/Footer';
import MainNav from './Components/MainNav';
import Home from './Pages/Home';
import Aboutme from './Pages/Aboutme';


function App() {
  return (
    <BrowserRouter basename='/whosbax'>
      <HashRouter>
        <MainNav />
        <Switch>
          <Link to='/' component={Home} />
          <Link to='/about-me' component={Aboutme} />
        </Switch>
        <Footer />
      </HashRouter>
    </BrowserRouter>
  );
}

export default App;
